// 1, import
import styled from "styled-components";
import Link from "next/link";

// 2, dom

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
  }
  handleChange = e => {
    this.setState({ value: e.target.value });
  };
  render() {
    return (
      <Container>
        <h2>留言</h2>
        <p>您的留言:</p>
        <textarea
          cols="30"
          rows="5"
          value={this.state.value}
          onChange={this.handleChange}
        />
      </Container>
    );
  }
}

export default Index;

